/* Container for the entire landing page */
.landing-container {
    width: 100%;
    padding: 0;
    margin: 0;
    font-family: 'Arial', sans-serif;
    background: #f3f4f6;
  }
  
  /* Header styles */
  .header {
    text-align: center;
    padding: 100px 20px;
    background: #8bc34a;
    color: #fff;
  }
  
  .header h1 {
    font-size: 3rem;
    margin-bottom: 20px;
  }
  
  .header-subtitle {
    font-size: 1.5rem;
    margin-bottom: 30px;
  }
  
  .header-button {
    padding: 10px 20px;
    font-size: 1rem;
    color: #8bc34a;
    background: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: all 0.3s ease;
  }
  
  .header-button:hover {
    background: #6b9b38;
    color: #fff;
  }
  
  /* Features section styles */
  .features {
    display: flex;
    justify-content: center;
    padding: 50px 20px;
    background: #ffffff;
  }
  
  .feature-card {
    flex: 1;
    max-width: 300px;
    margin: 0 20px;
    padding: 30px;
    text-align: center;
    background: #e8f5e9;
    border-radius: 8px;
    transition: all 0.3s ease;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  }
  
  .feature-card:hover {
    transform: translateY(-10px);
  }
  
  .feature-icon {
    font-size: 2.5rem;
    color: #8bc34a;
    margin-bottom: 20px;
  }
  
  /* About section styles */
  .about {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 100px 20px;
    background: #f3f4f6;
  }
  
  .about-content {
    max-width: 600px;
    text-align: center;
  }
  
  .about h2 {
    font-size: 2.5rem;
    margin-bottom: 20px;
    color: #333;
  }
  
  .about p {
    font-size: 1.2rem;
    margin-bottom: 30px;
    color: #555;
  }
  
  .about-button {
    padding: 10px 20px;
    font-size: 1rem;
    color: #fff;
    background: #8bc34a;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: all 0.3s ease;
  }
  
  .about-button:hover {
    background: #6b9b38;
  }
  